





.NavBar{
  width: 100%;
  height: 50px;
  top: 0px;
  background-color: #2C2F48;
  position: fixed;
  z-index: 12;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #01B7C5;
}

.btn-container{
  width: 20%;
  height: 100%;
  display: flex;

  align-items: center;
  border-right: 1px solid #01B7C5;
}

.search-container{
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #01B7C5;
}

.language-container{

  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn{
  position: relative;
  left: 5%;
  /* margin-left: 10px; */
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 15px;
  box-sizing: border-box;
  background-size: cover;
  border: none;
  /* background-color: linear-gradient(224deg, #00E5E5 8.72%, #72A5F2 50.87%, #E961FF 91.3%); */
  background: var(--dark-gradient, linear-gradient(224deg, #00E5E5 8.72%, #72A5F2 50.87%, #E961FF 91.3%));
}

img{
  width: 100%;
  background-size: cover;
}

.NavSearch{
  border: none;
}

@media screen and (max-width: 768px) {
  .NavBar{
    width: 100%;
    height: 50px;
    top: 0px;
    background-color: #2C2F48;
    position: fixed;
    z-index: 12;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #01B7C5;
  }

  .btn-container{
    height: 100%;
    display: flex; 
    align-items: center;
    border-right: none;
  }

  .search-container{
    width: 0;
    display: none;
  }

  .language-container{

    position: relative;
    right: 5%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}